
.btn-group {
  width: 100%;
  margin: 8px 0;

  display: flex;
  .btn {
    flex: 1;
    border: 2px solid rgb(185, 94, 109) !important;
    background-color: rgb(185, 94, 109);
    &.active {
      background-color: rgb(192, 133, 143) !important;
    }
    &:focus {
      box-shadow: none !important;
    }
  }
}

.btn-primary {
  color: white;
  font-size: medium;
  background-color: rgb(185, 94, 109);;
  border-color: rgb(185, 94, 109);;
  &:hover {
    background-color: rgb(192, 133, 143);
    border-color: #5bc4cf;
  }
}

.form-control {
  background-color: rgb(40,40,40);
  height: 4rem;
  font-size: medium;
  color: white;
  border: none;
  &:focus {
    background-color: rgb(40,40,40);
    color: white;
    border: none;
  }
  &:disabled {
    background-color: rgb(20,20,20);
    color: white;
    border: none;
  }
}

.input-group-append {
  .input-group-text {
    font-size: medium;
    background-color: black;
    border: 1px solid rgb(40,40,40);
    color: white;
  }
}
